 120, 165, 0.733);\r\n  margin-bottom: 30px;\r\n  /* margin-left: -50px; */\r\n  grid-area: cc;\r\n  animation: thirdd 1s steps(4, end) infinite alternate;\r\n}\r\n.pre_circ4 {\r\n  width: 30px;\r\n  height: 30px;\r\n  border-radius: 50%;\r\n  background-color: rgba(168, 88, 155, 0.733);\r\n  margin-bottom: 30px;\r\n  /* margin-left: -60px; */\r\n  grid-area: dd;\r\n  animation: fourth 1s steps(4, end) infinite alternate;\r\n}\r\n\r\n/* DESKTOP CSS CODES */\r\n.main_cont_desktop {\r\n  display: grid;\r\n  grid-template-columns: 25vw 50vw 25vw;\r\n  column-gap: 5px;\r\n  height: var(--height);\r\n  width: 100vw;\r\n}\r\n/* DESKTOP LEFT SIDE */\r\n.left_side_desktop {\r\n  background-color: rgba(255, 255, 255, 0.892);\r\n  height: 100vh;\r\n  /* border: var(--border2); */\r\n  display: grid;\r\n  grid-template-rows: 12% 65% 22%;\r\n  row-gap: 1%;\r\n  width: 100%;\r\n  /* padding: 5px 0px; */\r\n}\r\n.hotel_name_desktop {\r\n  /* border: var(--border); */\r\n  max-width: 25vw;\r\n  display: grid;\r\n  align-items: center;\r\n  padding: 0px 2px;\r\n}\r\n.hotel_name_desktop img {\r\n  width: 100%;\r\n  max-width: 100%;\r\n  height: 70%;\r\n  max-height: 100%;\r\n}\r\n.menu_bar_desktop {\r\n  display: grid;\r\n  padding-left: 1vw;\r\n  align-items: center;\r\n  /* border: var(--border3); */\r\n  max-width: 25vw;\r\n}\r\n.menu_bar_desktop a:nth-child(4) {\r\n  letter-spacing: normal;\r\n}\r\n\r\n.menu_bar_desktop a {\r\n  color: rgb(12, 9, 9);\r\n  /* border: var(--border2); */\r\n  background-color: var(--darkwhite);\r\n  white-space: nowrap;\r\n  padding: 10px;\r\n  letter-spacing: 3px;\r\n  font-size: clamp(15px, 1.5vw, 26px);\r\n  max-width: 23.5vw;\r\n  border-top-left-radius: 50px;\r\n  border-bottom-left-radius: 50px;\r\n  border-right: 2px solid rgb(26, 155, 235);\r\n}\r\n.menu_bar_desktop a:nth-child(2) {\r\n  color: rgb(25, 140, 211);\r\n  border-right: none;\r\n}\r\n.menu_bar_desktop a:hover {\r\n  color: rgb(25, 140, 211);\r\n  cursor: pointer;\r\n}\r\n.discount {\r\n  display: flex;\r\n  flex-direction: column;\r\n  text-align: center;\r\n  font-family: sans-serif;\r\n  background-image: url(./img/img2.jpg);\r\n  background-position: top;\r\n  background-size: 150%;\r\n  background-repeat: no-repeat;\r\n  filter: opacity(0.8);\r\n  /* border: var(--border2); */\r\n}\r\n\r\n.discount b {\r\n  padding-left: 8px;\r\n}\r\n\r\n/* DESKTOP MIDDLE SIDE */\r\n.mid_side_desktop {\r\n  /* border: var(--border); */\r\n  display: grid;\r\n  grid-template-rows: 0.5fr 0.3fr 2.1fr 1.2fr;\r\n  gap: 15px;\r\n  height: 100vh;\r\n  overflow: hidden;\r\n}\r\n.header_desktop {\r\n  /* border: var(--border2); */\r\n  display: grid;\r\n\r\n  place-content: center;\r\n\r\n  max-width: 50vw;\r\n}\r\n.header_desktop h2 {\r\n  font-family: \"aladin\", Helvetica;\r\n  letter-spacing: 5px;\r\n  font-style: italic;\r\n  text-shadow: 1px 2px 2px rgb(25, 140, 211);\r\n}\r\n.header_desktop h2:hover {\r\n  text-shadow: 2px 1px 1px rgba(25, 140, 211, 0.589);\r\n}\r\n.hotel_type_cont_desktop {\r\n  display: flex;\r\n  gap: 30px;\r\n  align-items: center;\r\n  height: 70%;\r\n  justify-content: flex-start;\r\n  letter-spacing: 3px;\r\n  max-width: 50vw;\r\n  overflow-x: auto;\r\n  border-bottom: 2px solid rgba(39, 141, 204, 0.673);\r\n  border-top: 2px solid rgba(39, 141, 204, 0.673);\r\n  padding: 0px 5px;\r\n}\r\n::-webkit-scrollbar {\r\n  height: 100%;\r\n  width: 0px;\r\n}\r\n.hotel_type_cont_desktop a {\r\n  /* border: var(--border); */\r\n  padding: 5px;\r\n  color: rgb(12, 9, 9);\r\n  height: 80%;\r\n  background-color: rgba(194, 217, 231, 0.673);\r\n  display: grid;\r\n  place-content: center;\r\n  box-shadow: 2px 2px 0px 2px rgba(183, 204, 217, 0.603) inset,\r\n    -2px -2px 0px 2px rgba(183, 204, 217, 0.603) inset;\r\n  /* border: var(--border); */\r\n}\r\n\r\n.hotel_type_cont_desktop a:hover {\r\n  background-color: rgba(39, 141, 204, 0.673);\r\n}\r\n/* FOR THE ROOMS IN DESKTOP VIEW */\r\n.main_rooms_cont_desktop {\r\n  display: flex;\r\n  /* bor